button>label {
  font-size: small;
  cursor: pointer;
}

#drawDocTopNavBar {
  color:inherit;
}

#drawDocTopNavBar>button:enabled {
  color:inherit;
}

#drawDocTopNavBar>*>button:enabled {
  color:inherit;
}

#drawDocToolBar {
  display: flex;
  color:inherit;
  flex-direction: column;
  height: 100%;
  width: 30px;
  border-right: 1px var(--GeneralBorderColor) solid;
}

#drawDocToolBar>button {
  color:inherit;
  font-size: 25px;
}

canvas-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: auto;
}

#drawCanvas {
  position: absolute;
  top: 1px;
  left: 1px;
  cursor: none;
  background: white;
  image-rendering: pixelated;
  image-rendering: crisp-edges;
}

#previewCanvas {
  position: absolute;
  top: 0px;
  left: 0px;
  background: transparent;
  image-rendering: pixelated;
  image-rendering: crisp-edges;
  border: 1px solid black;
}

canvas-container>textarea {
  position: absolute;
  background: transparent;
  border-style: dashed;
  width: auto;
}

#drawDocBottomNavBar {
  color: inherit;
  border-top: 1px var(--GeneralBorderColor) solid;
}

.cursorBrush {
  cursor:
    url("./Assets/GameIconsSVG/paint-brush-resized.svg") 52 75,
    auto;
}

.cursorBucket {
  cursor:
    url("./Assets/ThemifyIconsSVG/paint-bucket.svg") 2 28,
    auto;
}

.cursorPipette {
  cursor:
    url("./Assets/Css.gg/color-picker.svg") 4 20,
    auto;
}

.cursorSpray {
  cursor:
    url("./Assets/ThemifyIconsSVG/spray.svg") 0 2,
    auto;
}

.cursorEraser {
  cursor:
    url("./Assets/ThemifyIconsSVG/eraser.svg") 2 28,
    auto;
}

.cursorText {
  cursor: text;
}

.cursorMagnifier,
.cursorSelect,
.cursorShape {
  cursor: crosshair;
}